.card {
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    float: left;
    border: 10px solid #b5b5b5;
    border-radius: 8px;
    width: 19%;
    margin: 12px 30px;
    -webkit-box-shadow: -20px 20px 30px 0px rgba(0,0,0,.75);
            box-shadow: -20px 20px 30px 0px rgba(0,0,0,.75);
}

.card:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}


